import React, {Component} from 'react';
import './vehicle.css';

class  VehicleCU extends Component {
    
    componentWillReceiveProps(props) {
        console.log('componentWillReceiveProps: ' + JSON.stringify(props) + ' ' +  this.props.operation);
        this.refs.brand.value=props.item.brand;
        this.refs.model.value=props.item.model;
        this.refs.year.value=props.item.year;
        this.refs.color.value=props.item.color;
        this.refs.notes.value=props.item.notes;
        this.refs.clientId.value=props.item.clientId;
        this.refs.dailyPrice.value=props.item.dailyPrice;
        this.refs.rentedDays.value=props.item.rentedDays;
    }

    render(){
        if(!this.props['item']){
            return null;
        }

        if(!this.props['clients']){
            return null;
        }else{
            console.log(this.props.clients);
        }
        return (
            <div className='vehicleCard'>

                <input type="text" ref="brand" defaultValue={this.props.item.brand} placeholder="Brand"/><br/><br/>
                <input type="text" ref="model" defaultValue={this.props.item.model} placeholder="Model"/><br/><br/>
                <input type="number" ref="year" defaultValue={this.props.item.year} placeholder="Year"/><br/><br/>
                <input type="text" ref="color" defaultValue={this.props.item.color} placeholder="Color"/><br/><br/>
                <textarea ref="notes" defaultValue={this.props.item.notes} placeholder="Notes" cols="40" rows="10"/><br/><br/>
                <select ref="clientId" onChange={e=>{console.log(this.refs.clientId.value)}} defaultValue={this.props.item.clientId?this.props.item.clientId.id:-1}>
                    <option value='-1'>Select One...</option>
                    {this.props.clients.map((c)=>
                        <option key={c.id} value={c.id}>{c.name + ' ' + c.lastName}</option>
                    )}
                </select><br/><br/>
                <input ref="dailyPrice" defaultValue={this.props.item.dailyPrice} placeholder="dailyPrice"/><br/><br/>
                <input ref="rentedDays" defaultValue={this.props.item.rentedDays} placeholder="rentedDays"/><br/><br/>
                <button onClick={()=>{this.props.saveVehicle({
                    id:this.props.item.id,
                    brand:this.refs.brand.value,  
                    model:this.refs.model.value, 
                    year:this.refs.year.value, 
                    color:this.refs.color.value,
                    notes:this.refs.notes.value,
                    clientId:this.refs.clientId.value,
                    dailyPrice:this.refs.dailyPrice.value,
                    rentedDays:this.refs.rentedDays.value}, this.props.operation)}}>Save</button>
            </div>
        );
    }

}
/*onChange={()=>{
                    this.props.editFieldFunction("Phone Number",this.refs.phoneNumber.value)}} */
export default VehicleCU;
