import React from 'react';
import './vehicle.css';
import Vehicle from './vehicle'
import {connect} from 'react-redux';
import axios from 'axios';
import VehicleCU from './vehicleCreateUpdate';


function VehicleList(props) {
    var BASE_ADDRESS=''; //'https://localhost:44356/'; //props.baseAddress; //

    if(!props['vehicles']){
        props.dispatch((dispatch)=>{
            axios.get(BASE_ADDRESS + "api/Vehicles/")
            .then(resp=>{
                dispatch({type:'GET_VEHICLES', data:resp.data});
            })

        })
        return null;
    }
    if(!props['clients']){
        props.dispatch((dispatch)=>{
            axios.get(BASE_ADDRESS + "api/Clients/")
            .then(resp=>{
                dispatch({type:'GET_CLIENTS', data:resp.data});
            })

        })
        return null;
    }
    return (
        <div className="App">

            <button onClick={createVehicle}>Create Vehicle</button>
            <table>
                <tbody>
                    <tr>
                        <th>Brand / Model</th><th>Year / Color</th><th>Notes</th><th>Client</th><th>Daily Price / Rented Days</th><th>Image</th><th>Actions</th>
                    </tr>
                    {props['vehicles'].map((c, idx) => {
                        return <Vehicle item={c} key={c.id} cn={idx % 2} onEdit={editVehicle} onDelete={deleteVehicle}  />
                        }
                    )}
                </tbody>
            </table>

            {props['editVehicle'] != null &&
                <VehicleCU item={props['editVehicle']} operation={props['editVehicle'].operation} saveVehicle={saveVehicle} clients={props['clients']}/>
            }
        </div>
        
    )

    function editVehicle(id){
        props.dispatch((dispatch)=>{
            axios.get(BASE_ADDRESS + "api/Vehicles/"+id)
            .then(resp=>{
                resp.data.operation='UPDATE';
                dispatch({type:'GET_VEHICLE', data:resp.data});
            })

        })
    }

    function createVehicle(){
        
        props.dispatch({type:'GET_VEHICLE',data:{brand:'', model:'', year:'', color:'', notes:'', clientId:'', dailyPrice:'', rentedDays:'', operation:'CREATE'}});
    }
    
    function deleteVehicle(id){
        props.dispatch((dispatch)=>{
            axios.delete(BASE_ADDRESS + "api/Vehicles/"+id)
            .then(resp=>{
                    axios.get(BASE_ADDRESS + "api/Vehicles/")
                    .then(resp=>{
                        dispatch({type:'GET_VEHICLES', data:resp.data});
                    })
            })

        })
    }

    function saveVehicle(item, operation){
        console.log('saveVehicle', item, operation);
        /*if(item.clientId>0){
            axios.get(BASE_ADDRESS + "api/Clients/"+item.clientId)
            .then(resp=>{
                item.client=resp.data;
                

            })
        }*/

        if (operation === 'UPDATE') {
            axios
                .put(BASE_ADDRESS + "api/Vehicles/" + item.id, item)
                .then(res => {
                    props.dispatch((dispatch) => {
                        axios.get(BASE_ADDRESS + "api/Vehicles/")
                            .then(resp => {
                                dispatch({ type: 'GET_VEHICLES', data: resp.data });
                                dispatch({type:'RESET_VEHICLE'});
                            })

                    })
                })
                .catch(err => { }
                );
        }
        if(operation === 'CREATE'){
            axios
                .post(BASE_ADDRESS + "api/Vehicles/", item)
                .then(res => {
                    props.dispatch((dispatch) => {
                        axios.get(BASE_ADDRESS + "api/Vehicles/")
                            .then(resp => {
                                dispatch({ type: 'GET_VEHICLES', data: resp.data });
                                dispatch({type:'RESET_VEHICLE'});
                            });


                    })
                })
                .catch(err => { }
                );
        }


    }

    /*function clientFieldEdition(fieldName, value){
        console.log('clientFieldEdition', fieldName, value);
    }

    function clientSave(){
        console.log('clientSave');
    }*/

}

export default connect((state, props)=>{
    console.log('connect state', state);
    console.log('connect props', props);

    return{
        vehicles:state.vehicles,
        editVehicle:state.editVehicle,
        clients:state.clients
    }

})(VehicleList);