import React from 'react';
import './vehicle.css';

function Vehicle(props) {
    var classN = props.cn===0?'rowDark':'rowClear';
    if(!props['item']){
        return null;
    }
    return (
        <tr className={classN}>
            <td>{props.item.brand} | {props.item.model}</td>
            <td>{props.item.year} | {props.item.color}</td>
            <td>{props.item.notes}</td>
            <td>{props.item.client?props.item.client.name + ' ' + props.item.client.lastName:''}</td>
            <td>{props.item.dailyPrice} | {props.item.rentedDays}</td>
            <td><img src={props.item.imageUrl} /></td>
            <td><button onClick={()=>{props.onEdit(props.item.id)}}>Edit</button>&nbsp;&nbsp;
            <button onClick={()=>{props.onDelete(props.item.id)}}>Delete</button></td>
        </tr>
    );


}

export default Vehicle;

