import React, {Component} from 'react';
import './client.css';

class  ClientCU extends Component {
    
    componentWillReceiveProps(props) {
        console.log('componentWillReceiveProps: ' + JSON.stringify(props) + ' ' +  this.props.operation);
        this.refs.name.value=props.item.name;
        this.refs.lastName.value=props.item.lastName;
        this.refs.phoneNumber.value=props.item.phoneNumber;
        
    }

    render(){
        if(!this.props['item']){
            return null;
        }
        return (
            <div className='clientCard'>
                <input type="text" ref="name" defaultValue={this.props.item.name} placeholder="Name"/>
                <input type="text" ref="lastName" defaultValue={this.props.item.lastName} placeholder="Last Name"/>
                <input type="text" ref="phoneNumber" defaultValue={this.props.item.phoneNumber} placeholder="Phone Number"/>
                <button onClick={()=>{this.props.saveClient({id:this.props.item.id, name:this.refs.name.value, lastName:this.refs.lastName.value, phoneNumber:this.refs.phoneNumber.value }, this.props.operation)}}>Save</button>
            </div>
        );
    }

}
/*onChange={()=>{
                    this.props.editFieldFunction("Phone Number",this.refs.phoneNumber.value)}} */
export default ClientCU;
