import React from 'react';
import './client.css';

function Client(props) {
    var classN = props.cn===0?'rowDark':'rowClear';
    if(!props['item']){
        return null;
    }
    return (
        <tr className={classN}>
            <td>{props.item.name}</td>
            <td>{props.item.lastName}</td>
            <td>{props.item.phoneNumber}</td>
            <td><button onClick={()=>{props.onEdit(props.item.id)}}>Edit</button>&nbsp;&nbsp;
            <button onClick={()=>{props.onDelete(props.item.id)}}>Delete</button></td>
        </tr>
    );


}

export default Client;

