import React from 'react';
import './client.css';
import Client from './client'
import {connect} from 'react-redux';
import axios from 'axios';
import ClientCU from './clientCreateUpdate';


function ClientList(props) {
    var BASE_ADDRESS=''; //'https://localhost:44356/'; //props.baseAddress; //

    if(!props['clients']){
        props.dispatch((dispatch)=>{
            axios.get(BASE_ADDRESS + "api/Clients/")
            .then(resp=>{
                dispatch({type:'GET_CLIENTS', data:resp.data});
            })

        })
        return null;
    }
    return (
        <div className="App">
            <button onClick={createClient}>Create Client</button>
            <table>
                <tbody>
                    <tr>
                        <th>Name</th><th>Last Name</th><th>Phone Number</th><th>Actions</th>
                    </tr>
                    {props['clients'].map((c, idx) => {
                        return <Client item={c} key={c.id} cn={idx % 2} onEdit={editClient} onDelete={deleteClient} />
                        }
                    )}
                </tbody>
            </table>

            {props['editClient'] != null &&
                <ClientCU item={props['editClient']} operation={props['editClient'].operation} saveClient={saveClient} />
            }
        </div>
        
    )

    function editClient(id){
        props.dispatch((dispatch)=>{
            axios.get(BASE_ADDRESS + "api/Clients/"+id)
            .then(resp=>{
                resp.data.operation='UPDATE';
                dispatch({type:'GET_CLIENT', data:resp.data});
            })

        })
    }

    function createClient(){
        props.dispatch({type:'GET_CLIENT',data:{name:'', lastName:'', phoneNumber:'', operation:'CREATE'}});
    }
    
    function deleteClient(id){
        props.dispatch((dispatch)=>{
            axios.delete(BASE_ADDRESS + "api/Clients/"+id)
            .then(resp=>{
                    axios.get(BASE_ADDRESS + "api/Clients/")
                    .then(resp=>{
                        dispatch({type:'GET_CLIENTS', data:resp.data});
                    })
            })

        })
    }

    function saveClient(item, operation){
        console.log('saveClient', item, operation);
        if (operation === 'UPDATE') {
            
            axios
                .put(BASE_ADDRESS + "api/Clients/" + item.id, item)
                .then(res => {
                    props.dispatch((dispatch) => {
                        axios.get(BASE_ADDRESS + "api/Clients/")
                            .then(resp => {
                                dispatch({ type: 'GET_CLIENTS', data: resp.data });
                                dispatch({type:'RESET_CLIENT'});
                            })

                    })
                })
                .catch(err => { }
                );
        }
        if(operation === 'CREATE'){
            axios
                .post(BASE_ADDRESS + "api/Clients/", item)
                .then(res => {
                    props.dispatch((dispatch) => {
                        axios.get(BASE_ADDRESS + "api/Clients/")
                            .then(resp => {
                                dispatch({ type: 'GET_CLIENTS', data: resp.data });
                                dispatch({type:'RESET_CLIENT'});
                            });


                    })
                })
                .catch(err => { }
                );
        }

    }

    /*function clientFieldEdition(fieldName, value){
        console.log('clientFieldEdition', fieldName, value);
    }

    function clientSave(){
        console.log('clientSave');
    }*/

}

export default connect((state, props)=>{
    console.log('connect state', state);
    console.log('connect props', props);

    return{
        clients:state.clients,
        editClient:state.editClient
    }

})(ClientList);