import {applyMiddleware, createStore}from 'redux';
import {createLogger} from 'redux-logger';
import thunk  from 'redux-thunk';

var defaultState={
    
}

function client(state=defaultState, action){
    if(action.type==='GET_CLIENTS'){
        return {
            ...state,
            clients:action.data
        }
    }
    
    if(action.type==='GET_CLIENT'){
        return{
            ...state,
            editClient:action.data
        }
    }

    if(action.type==='RESET_CLIENT'){
        return {
            ...state,
            editClient:null
        }
    }

    if(action.type==='GET_VEHICLES'){
        return {
            ...state,
            vehicles:action.data
        }
    }

    if(action.type==='GET_VEHICLE'){
        return{
            ...state,
            editVehicle:action.data
        }
    }
    
    if(action.type==='RESET_VEHICLE'){
        return {
            ...state,
            editVehicle:null
        }
    }


    return state;
}

var logger = createLogger({
    collapsed:true
})

var store = createStore(
    client,
    applyMiddleware(thunk, logger)
    );

export default store;